/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react";
import PropTypes from "prop-types";
import { useStaticQuery, graphql } from "gatsby";
import CssBaseline from "@material-ui/core/CssBaseline";
import { ThemeProvider } from "@material-ui/core/styles";
import { css } from "@emotion/core";
import { useIntl } from "gatsby-plugin-intl";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";
import RestoreIcon from "@material-ui/icons/Restore";
import FavoriteIcon from "@material-ui/icons/Favorite";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import Box from "@material-ui/core/Box";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { Link } from "gatsby-plugin-intl";
import Headroom from "react-headroom";
import theme from "../../src/theme";
import SEO from "./seo";
// import "../assets/styles.scss";
import Header from "./Header";
import HeaderMobileView from "./HeaderMobileView";

const useStyles = makeStyles(theme => ({
  "@global": {
    ul: {
      margin: 0,
      padding: 0,
      listStyle: "none",
    },
    a: {
      color: theme.palette.primary.main,
      textDecoration: "none",
    },
  },
  stickToBottom: {
    width: "100%",
    position: "fixed",
    bottom: 0,
  },
  footer: {
    // borderTop: `1px solid ${theme.palette.divider}`,
    marginTop: theme.spacing(8),
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.up("sm")]: {
      paddingTop: theme.spacing(6),
      paddingBottom: theme.spacing(6),
    },
  },
}));
const Layout = ({
  isFullWidth,
  isDontShowBack = false,
  mobileHeaderFilterView,
  children,
  backMobileLink,
}) => {
  const intl = useIntl();
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const isMobileView = useMediaQuery(theme.breakpoints.down("sm"));
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  return (
    <>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Headroom
          style={{
            background: "#FFFFFF",
            boxShadow: "1px 1px 1px rgba(0,0,0,0.25)",
          }}
        >
          {!isMobileView && (
            <Header siteTitle={data.site.siteMetadata.title} isFullWidth />
          )}
          {isMobileView && (
            <>
              <HeaderMobileView
                siteTitle={data.site.siteMetadata.title}
                isDontShowBack={isDontShowBack}
                isFullWidth
                backMobileLink={backMobileLink || "/"}
              />
              {mobileHeaderFilterView}
            </>
          )}
        </Headroom>

        {isFullWidth && <main>{children}</main>}
        {!isFullWidth && (
          <Container fixed>
            <main
              css={css`
                margin: 1rem 0;
              `}
            >
              {children}
            </main>
          </Container>
        )}
        {/* <footer>© GladWedding</footer> */}
        <footer
          css={css`
            background-color: #fff5f8;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 16px;
          `}
        >
          <div
            css={css`
              flex-direction: column;
              display: flex;
              align-items: center;
              justify-content: center;
            `}
          >
            <p>
              <Link
                to={`/contactus`}
                color="secondary"
                css={css`
                  text-decoration: none;
                `}
              >
                Contact Us
              </Link>
            </p>

            <p>© GladWedding</p>
          </div>
        </footer>

        {/* <Container maxWidth="md" component="footer" className={classes.footer}>
          <Box mt={5}>
            <p>© GladWedding</p>
          </Box>
        </Container> */}
      </ThemeProvider>
    </>
  );
};

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout;
